.login-container {
    display: flex;
    flex-direction: column;
    height: 100vh;

    .logo {
        width: 200px;
        margin: 20px;
    }

    .image-form-container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-evenly;
        background-color: #F1F6F7;
        flex-wrap: wrap;
        height: 100%;
        
        .banner-container {
            flex: 0.7;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            .banner-image {
                width: 70%;
            }
        }

        .form-container {
            flex: 0.3;
            background-color: #fff;
            margin: 50px;
            border-radius: 20px;
            height: 80%;
            width: 100%;
            padding: 20px;

            .form-title {
                font-weight: 700;
                color: #444;
                font-size: 1.4rem;
                margin-bottom: 5px;
            }

            .sub-title {
                font-weight: 500;
                color: #444;
                font-size: 1rem;
                margin-bottom: 50px;
            }

            .field-tag {
                margin-top: 20px;
                font-size: 1rem;
                color: #444;
                font-weight: 500;
            }   

            .input-field {
                border: none;
                background-color: #F1F6F7;
                border-radius: 5px;
                margin-top: 5px;
                padding: 5px 10px;
                color: #444;
                font-size: 1.1rem;
                width: 100%;
                outline: none;
            }

            .submit-button {
                font-weight: 500;
                color: #fff;
                font-size: 1rem;
                width: 50%;
                margin-top: 50px;
                margin-left: 25%;
            }
        }
    }
}